const getTocData = item => {
  const getElement = element => {
    const aElement = element.children.find(x => x.tagName === "a")
    const href = aElement?.properties.href.replace(".md", "")
    const textElement = aElement?.children.find(x => x.type === "text").value

    return {
      href: href,
      textElement: textElement,
      children: [],
    }
  }

  const list = item?.toc?.htmlAst?.children.find(
    x => typeof x === "object" && x.tagName === "ul"
  ).children
  const elements = list?.filter(x => x.tagName === "li")
  let data = []

  if (elements) {
    for (const element of elements) {
      let result = getElement(element)

      const subList = element.children?.find(x => x.tagName === "ul")

      if (subList) {
        const subElement = subList.children.filter(x => x.tagName === "li")
        result.children = subElement.map(x => getElement(x))
      }
      data.push(result)
    }
  }
  return data
}

// eslint-disable-next-line no-undef
module.exports.getTocData = getTocData
