import * as React from "react"
import { useMemo } from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { graphql } from "gatsby"
import Toc from "../components/toc/toc"
import renderAst from "../utils/render-ast"
import renderHomepageAst from "../utils/render-homepage-ast"
import { Flex, Box, VStack } from "@chakra-ui/react"
import Menu from "../components/menu/menu"
import { getTocData } from "../utils/get-toc-data"
import Feedback from "../components/feedback/feedback"
import { useState, useEffect } from "react"

const DocumentationPage = ({ data, location }) => {
  const [offset, setOffset] = useState(0)
  console.log(data)
  useEffect(() => {
    const onScroll = () => setOffset(window.pageYOffset)
    window.addEventListener("scroll", onScroll, { passive: true })
    return () => window.removeEventListener("scroll", onScroll)
  }, [])

  const tocData = useMemo(() => {
    return getTocData(data)
  }, [data])

  const page = data.pageContent
  const pageType = page.fileAbsolutePath.includes("/home-pages/")
    ? "homePage"
    : "docPage"

  return (
    <Layout tocContents={data.rootToc} location={location}>
      <Seo title={page.frontmatter.title} />

      {pageType === "docPage" ? (
        <Box p={{ base: 4, md: 8, lg: 10 }} display="flex">
          <Flex w="100%" pt={{ base: "none", md: 16 }} display={{ md: "flex" }}>
            <Box
              pt={5}
              mb={{ base: 10, md: "none" }}
              minW={307}
              mr={{ base: "none", md: "none", lg: "none", xl: 10 }}
            >
              <Menu
                content={tocData}
                rootTitle={data.toc}
                location={location}
              />
            </Box>
            <Box
              flex="1"
              alignItems="center"
              justifyContent="center"
              px={{ md: "0" }}
              pt={5}
            >
              <Box>{renderAst(page.htmlAst)}</Box>
            </Box>
            <Box
              display={{ base: "none", md: "none", lg: "flex", xl: "flex" }}
              pt={5}
              ml="3.5em"
              w={{ lg: "12em", xl: "18em" }}
            >
              <VStack
                position="fixed"
                overflow="hidden"
                zIndex={1}
                top={offset > 150 ? 104 : ""}
              >
                <Box>
                  <Flex overflow="auto" maxW="18em" maxH="60vh">
                    <Toc data={data.pageContent} location={location} />
                  </Flex>

                  <Feedback location={location} />
                </Box>
              </VStack>
            </Box>
          </Flex>
        </Box>
      ) : (
        <Box m={10}>
          <div>{renderHomepageAst(page.htmlAst)}</div>
        </Box>
      )}
    </Layout>
  )
}

export const query = graphql`
  query ($absolutePath: String!, $tocRegex: String!) {
    pageContent: markdownRemark(fileAbsolutePath: { eq: $absolutePath }) {
      html
      htmlAst
      fileAbsolutePath
      frontmatter {
        title
      }
      headings {
        id
        value
        depth
      }
    }
    toc: markdownRemark(fileAbsolutePath: { regex: $tocRegex }) {
      html
      htmlAst
      fileAbsolutePath

      frontmatter {
        title
      }
    }
    rootToc: allFile(
      filter: { sourceInstanceName: { eq: "homepages-main-fixed" } }
    ) {
      nodes {
        id
        gitRemote {
          sourceInstanceName
        }
        name
      }
    }
  }
`

export default DocumentationPage
