import React from "react"
import { Box } from "@chakra-ui/react"

const Tr = ({ children }) => {
  return (
    <Box
      as="tr"
      borderColor="rlBlack40"
      borderWidth="1px"
      _even={{
        bg: "#F5F5F5",
      }}
    >
      {children}
    </Box>
  )
}

export default Tr
